.searchWrapper {
   display: flex;
   justify-content: center;
   align-items: center;
}

.searchBar {
   padding: 10px;
   border-radius: 6px;
   border: 1px solid;
   width: 60%;
}

.searchBar:focus {
   outline: none!important;
   border: 1px solid transparent;
   box-shadow: 0px 0px 6px 2px rgb(171, 130, 238);
}
